.home-panels-wrapper {
    padding: 5rem 0;
    background-color: rgba(black, 0.65);
    box-shadow:0 0 5rem #cfae8b inset;
}
.home-panels-wrapper::after {
    content:''; 
    position:absolute;
    height:100%;
    z-index:-1;
    width:100%;
    //padding of parent element
    margin-top:-5rem;
    background: url('../../assets/background-images/main-background.jpg') center center fixed no-repeat;
    background-size: cover;
}